import React from 'react'
import Helmet from 'react-helmet'
import Offerings from '../Offerings'
import Testimonials from '../Testimonials'
import PropTypes from 'prop-types'

const HomePageTemplate = ({
  title,
  heading,
  description,
  offerings,
  meta_title,
  meta_description,
  testimonials,
}) => (
    <div>
      <Helmet>
        <title>{meta_title}</title>
        <meta name='description' content={meta_description} />
      </Helmet>
      <section className='hero is-primary is-bold is-medium'>
        <div className='hero-body'>
          <div className='container'>
            <div className='columns'>
              <div className='column is-10 is-offset-1'>
                <div className='section'>
                  <h1 className='title has-text-centered is-size-1'>
                    Shri Vitthal Hindu Mandir
                  </h1>
                  <p class="block has-text-centered is-size-3"><a class="is-link is-3" href="#about">link</a></p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className='section section--gradient'>
        <div className='container'>

          <div className='section block'>
            <div className='content'>
              <div>
                <h2 className='content has-text-centered has-text-weight-semibold is-size-2 is-uppercase is-large'>
                  Welcome to Svhms
                </h2>
                <p class="block"></p>
                <div className="block">
                  <p className='content is-size-5'>Shri Vitthal Hindu Mandir (SVHM) is one of Mississauga’s longest running registered non-profit devoted to bringing families together, providing a safe place of worship, assisting new comers transition into the Canadian life, and preserving old traditions while embracing new ones. </p>
                  <p className='content is-size-5'>Established in 1996 in the beautiful city of Mississauga, Ontario, Canada — one of Canada’s fastest growing and diverse cities — we abide by the old simple code known as the “System of Vedanta” which combines religion, philosophy, and innovative nature of the Hindu's.  The Vedanta teaches us that we are all divine, regardless of creed, class, gender, and ethnicity, and that our souls divinity may be manifested through worship, contemplation, and unselfish work.  At SVHM we welcome everyone.</p>
                  <p className='content is-size-5'>The Mandir is located close to Toronto, Brampton, Oakville, Richmond Hill, Milton, and Etobicoke. Come visit us today and join our community, all are welcome!</p>
                </div>
              </div>
            </div>
            {/* <Testimonials testimonials={testimonials} /> */}
          </div>

          <div className='section block'>
            <div className='content'>
              <div>
                <iframe width="1180" height="664" src="https://www.youtube.com/embed/8HGSDKG1Fqk" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                <p></p>
                <div className="block">
                  <h2 className='content has-text-centered has-text-weight-semibold is-size-2 is-uppercase is-large'>
                    HINDU TEMPLE IN MISSISSAUGA
                  </h2>
                  <p className='content is-size-5'> SVHM is different from other the other temples and mandir’s in and around Toronto, Richmond Hill, Brampton, and Mississauga as it is more family oriented and transparent. The temple does not fund any external political groups and adheres to CRA policies for non profit organizations. </p>
                </div>
              </div>
            </div>
          </div>

          <div className="section block">
            <div className="content">
              <p className="content count is-size-4">
                "Immigrating from Northern India in 2016, I did not find a facility in Toronto that provided a place where I can pray, mediate, practice yoga, and meet other Canadian-Tibetans like myself. Then I discovered SVHM, which has been instrumental in my journey of transitioning into the Canadian lifestyle, helping me establish myself in the GTA, build a network of quality peers who share my values, and where I can peacefully pray."
              </p>
              <div className="columns is-vcentered is-hcentered" style={{}}>
                <div className="column">
                  <img src="https://shrivitthal.com/wp-content/uploads/2018/02/25497478040_140ce47f31_k.jpg" alt="" />
                </div>
                <div className="column">
                  <p className="is-size-5  is-uppercase">
                    TSERING WANGCHUK
                  </p>
                  <p className="is-size-6">
                    Tibetan-Canadian
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div className='section block'>
            <div className='content'>
              <h3 className='content is-size-3'> A temple is what you want it to be. For the devotee it is the place where god lives. For the connoisseur of art, the sculpture and paintings make a temple a living museum. For a dancer the figures carved on the walls are the foundation of her art. For a musician or a poet the deity is both inspiration and the final audience. You don’t have to enter a temple to pray. You can go there to appreciate the many aspects of India’s rich and iridescent culture.</h3>
            </div>
          </div>






        </div>
      </section>


    </div>
  )

HomePageTemplate.propTypes = {
  title: PropTypes.string,
  meta_title: PropTypes.string,
  meta_description: PropTypes.string,
  heading: PropTypes.string,
  description: PropTypes.string,
  offerings: PropTypes.shape({
    blurbs: PropTypes.array,
  }),
  testimonials: PropTypes.array,

}

export default HomePageTemplate
